/**
 * Eric Meyer's Reset CSS v2.0 (http://meyerweb.com/eric/tools/css/reset/)
 * http://cssreset.com
 */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header,
menu, nav, output, ruby, section, summary,
time, mark, audio, video, input {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font-weight: normal;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
/* custom */
a {
    color: #7e8c8d;
    text-decoration: none;
    -webkit-backface-visibility: hidden;
}
li {
    list-style: none;
}
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}
::-webkit-scrollbar-track-piece {
    background-color: rgba(0, 0, 0, 0.2);
    -webkit-border-radius: 6px;
}
::-webkit-scrollbar-thumb:vertical {
    height: 5px;
    background-color: rgba(125, 125, 125, 0.7);
    -webkit-border-radius: 6px;
}
::-webkit-scrollbar-thumb:horizontal {
    width: 5px;
    background-color: rgba(125, 125, 125, 0.7);
    -webkit-border-radius: 6px;
}
html, body {
    width: 100%;
}
body {
    -webkit-text-size-adjust: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
#app {
  width: 100%;
  height: 100%;
  font-family: "PingFang SC", "Microsoft YaHei", STHeiti, "WenQuanYi Micro Hei", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e1f9e7;
  color: #1e222e;
}
#app .header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  width: 100%;
  height: 60px;
  line-height: 60px;
  background: rgba(255,255,255,0.98);
  -webkit-box-shadow: 0 0 3px rgba(14,14,14,0.26);
          box-shadow: 0 0 3px rgba(14,14,14,0.26);
}
#app .header nav {
  padding: 0 80px;
}
#app .header nav .logo {
  display: inline-block;
  padding-top: 5px;
  margin-right: 5px;
}
#app .header nav .title {
  display: inline-block;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  line-height: 10px;
  font-size: 26px;
  color: #4f8b78;
}
#app .header nav .menu {
  float: right;
}
#app .header nav .menu .list-item {
  display: inline-block;
  margin: 0 8px;
}
#app .header nav .menu .list-item a {
  font-size: 16px;
  color: #34495e;
}
#app .header nav .menu .list-item a:hover {
  color: #42b983;
}
#app .photos {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 100px 200px 0 200px;
}
#app .photos .photo {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  height: 198px;
  width: 195px;
  margin-bottom: 20px;
  margin-right: 20px;
}
#app .footer {
  padding: 30px 0;
  text-align: center;
  line-height: 24px;
}
#app .footer a {
  color: #2a3e31;
}
.photo-wrapper {
  display: inline-block;
  position: relative;
  width: 195px;
  height: 198px;
  text-align: center;
  overflow: hidden;
  background-color: #fff;
}
.photo-wrapper .photo-content {
  padding: 9px 7.5px;
  overflow: hidden;
}
.photo-wrapper .photo-desc {
  position: absolute;
  padding: 7.5px 0;
  left: 0;
  bottom: 0;
  height: 25.35px;
  width: 100%;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  background-color: rgba(255,255,255,0.85);
  text-align: center;
  font-size: 15px;
  color: #6e7799;
}
.photo-wrapper .photo-desc.move-enter-active,
.photo-wrapper .photo-desc.move-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.photo-wrapper .photo-desc.move-enter,
.photo-wrapper .photo-desc.move-leave-to {
  -webkit-transform: translateY(25.35px);
          transform: translateY(25.35px);
  opacity: 0;
}